import "bootstrap/dist/css/bootstrap.min.css";
import "bootstrap/dist/js/bootstrap.bundle.min";
import 'react-date-picker/dist/DatePicker.css';
import 'react-calendar/dist/Calendar.css';
import { Button } from "react-bootstrap";
import DatePicker from "react-date-picker";
import React, { useState } from "react";
import Axios, * as others from 'axios';
import { ReactDOM } from "react";
import Page from "./Page";
import './App.css';

function App() {
  


  const [value, onChange] = useState(new Date());
  const [formData, setFormData] = useState({ start: '', destination: '' });
  let obj={};
  let bool=false;
  const onChangeHandler = (event) => {
  
    console.log(event)
    if (event.target.name === 'languages') {

      let copy = { ...formData }

      if (event.target.checked) {
        copy.languages.push(event.target.value)
      } else {
        copy.languages = copy.languages.filter(el => el !== event.target.value)
      }

      setFormData(copy)

    } else {
      setFormData(() => ({
        ...formData,
        [event.target.name]: event.target.value
      }))
    }
  }
  const onSubmitHandler = async (event) => {
    event.preventDefault();
    console.log(formData,value)
  
    console.log(value.getFullYear(),value.getMonth(),value.getDate())
    value.date=value.getDate+1;
    await Axios.post('/',{
      start:formData.start,
      destination:formData.destination,
      date:value
    }).then((res)=>{
      console.log(res.data)
      obj=res.data;
      bool=true;
    }
    )
    .catch((err)=>{
      console.log(err)
    }
    )
    
  }
  

  return (

    <div className="App">
 
   
  <form onSubmit={onSubmitHandler} >
        <div className="form-group">
          <label htmlFor="name" className="form-label">Start</label>
          <input className="form-control" name="start" onChange={onChangeHandler} value={formData.start} />
        </div>
        <div className="form-group">
          <label htmlFor="email" className="form-label">Destination</label>
          <input className="form-control" name="destination" onChange={onChangeHandler} value={formData.destination} />
        </div>
        <div>
        <div>
      <DatePicker onChange={onChange} value={value} />
    </div>
          </div>
        <div className="form-group">
          <Button variant="primary" className="btn" type='submit'>Submit</Button>{' '}
        </div>
      </form> 
    </div>
  );
}

export default App;
